.header{
   height: 400px;
   background-image: url('../images/street-640.jpg');
   background-repeat: no-repeat;
   background-position: center;
   opacity: .9;
   @media (min-width: 640px){
    height: 800px;
    background-image: url('../images/street-1280.jpg');
   }
   @media (min-width: 1280px){
    height: 1200px;
    background-image: url('../images/street-1920.jpg');
   }
   
   &__title{
       font-size: 3em;
       text-align: center;
       padding: 5px;
       color: #fff;
       text-shadow: 5px 3px #000;
       text-transform: uppercase;
       @media (min-width: 640px){
        font-size: 3.6em;
       }
       @media (min-width: 1280px){
        font-size: 6em;
        letter-spacing: 4px;
       }
   }
   &__area{
        position: absolute;
        top: 205px;
        left: 0px;
       width: 85%;
       height: 80px;
       background-color:orange;
       opacity: .5;
       box-shadow: 1px 1px 1px 1px black;
      @media (min-width: 330px){
        height: 70px;
    }
        @media (min-width: 360px){
        width: 72%;
        height: 90px;
    }   
        @media (min-width: 420px){
        height: 80px;
    }
       @media (min-width: 580px){
        height: 90px;
    }
       @media (min-width: 604px){
        height: 70px;
    }
        @media (min-width: 640px){
        top: 335px;
        height: 170px;
    }
       @media (min-width: 726px){
        height: 140px;
    }
        @media (min-width: 820px){
        height: 150px;
    }
        @media (min-width: 941px){
        height: 160px;
    }
    @media (min-width: 980px){
        height: 140px;
    }
    @media (min-width: 1024px){
        width: 76%;
        height: 270px;
    }
    @media (min-width: 1062px){

        height: 240px;
    }
    @media (min-width: 1280px){
        top: 520px;
        height: 250px;
    } 
    @media (min-width: 1406px){
        height: 280px;
    }
    @media (min-width: 1582px){
        height: 230px;
    }   
   }
    &__description{
    position: relative;
    width: 80%;
    text-align: right;
    font-size: 1.2em;
    text-shadow: 1px 1px #fff;
    margin-top: 120px;
    margin-left: 5px;
    font-weight: 600;
    line-height: 1.1;
    @media (min-width: 360px){
        width: 70%;
        font-size: 1.3em;
       }
    @media (min-width: 396px){
        margin-top: 160px;
       }
    @media (min-width: 469px){
        font-size: 1.4em;
       }
    @media (min-width: 530px){
        font-size: 1.5em;
       }
    @media (min-width: 580px){
        font-size: 1.6em;
       }
    @media (min-width: 640px){
        margin-top: 300px;
        font-size: 2.3em;
       }
       @media (min-width: 820px){
        font-size: 2.5em;
       }
       @media (min-width: 960px){
        font-size: 2.6em;
       }
        @media (min-width: 1024px){
        font-size: 3.6em;
        line-height: 1.2;
        width: 75%;
    }
    @media (min-width: 1100px){
        font-size: 3.7em;
    }
    @media (min-width: 1280px){
        margin-top: 450px;
        font-size: 4em;
    }
    @media (min-width: 1406px){
        font-size: 4.4em;
    }
    @media (min-width: 1547px){
        font-size: 4.5em;
    }
  }
  &__slogan{
      position: absolute;
      right: 5px;
      top: 290px;
      font-weight: bold;
      font-size: 2.2em;
      color: #FF5376;
      text-shadow: 2px 1px #fff;
      width: 30%;
      text-align: center;
    @media (min-width: 420px){
        font-size: 2.3em;
        right: 15px;
    }
    @media (min-width: 640px){
        top: 550px;
        right: 10px;
        font-size: 4.6em;
        width: 30%
    }
    @media (min-width: 1024px){
        top: 580px;
        right: 20px;
        font-size: 4.7em;
        width: 20%;
    }
    @media (min-width: 1280px){
        top: 780px;
        font-size: 5.6em;

    }
    
  }
   
}