.quote{
    min-height: 30vh;
    color: #ccc;
    margin: 5% 20%;
    &__quotation{
        font-size: 3em;
        text-transform: uppercase;
        font-weight: 700;
        @media(min-width: 550px){
            font-size: 4em;
        }
        @media(min-width: 950px){
            font-size: 5em;
        }
    }
    &__author{
        margin: 15px 0;
        font-size: 1.5em;
        @media(min-width: 550px){
            font-size: 2em;
        }
        @media(min-width: 950px){
            margin: 40px 0;
            font-size: 2.3em;
        }
    }
}