.footer{
    margin-top: 20px;
    min-height: 60vh;
    color: cornsilk;
    font-size: 1.2em;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    &__subscription{
        flex-basis:100%;
    }
    &__customer{
        flex-basis:33%;
    }
    &__company{
        flex-basis:33%;
    }
    &__social{
        flex-basis:33%;
    }
    &__sign{
        background-color: lightcoral;
        flex-basis: 100%;
    }
}
.subscription{
    &__caption{
        padding: 20px;
        font-size: 2em;
    }
    &__input{
        width: 75%;
        height:50px;
        text-align: center;
    }
}

.customer{
    display: flex;
    flex-direction: column;
    &__caption{
        color: #e63946;
        font-size: 1.4em;
    }
    &__list{
        margin-top: 10px;
        list-style-type: none;
        display: flex;
        flex-direction: column;
        align-content: center;
        &__item{
            display: block;
            margin: 10px;
        }
    }
    
}

.company{
    display: flex;
    flex-direction: column;
    &__caption{
        color: #e63946;
        font-size: 1.4em;
    }
    &__list{
        margin-top: 10px;
        list-style-type: none;
        display: flex;
        flex-direction: column;
        align-content: center;
        &__item{
            display: block;
            margin: 10px;
        }
    }
    
}

.social{
    display: flex;
    flex-direction: column;
    &__caption{
        color: #e63946;
        font-size: 1.4em;

    }
    &__list{
        margin-top: 10px;
        list-style-type: none;
        display: flex;
        flex-direction: column;
        align-content: center;
        &__item{
            display: block;
            margin: 10px;
        }
    }
    
}