.icons{
    min-height: 40vh;
    height: 60vh;
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    &__simple{
        cursor: pointer;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-basis: 33%; 
        flex-grow: 1;
        height: 50%;
        font-size: 8em;
        color: #f1faee;
        transition: 1s;
    &--one{
        background-color: #457b9d;
    }
    &--one:hover{
        background-color: #e63946;
        color: #a8dadc;
    }
    &--two{
        background-color:#1d3557;
    }
    &--two:hover{
        background-color: #e63946;
        color: #a8dadc;
    }
    &--three{
        background-color: #457b9d;
    }
    &--three:hover{
        background-color: #e63946;
        color: #a8dadc;
    }
    &--four{
        background-color:#1d3557;
    }
    &--four:hover{
        background-color: #e63946;
        color: #a8dadc;
    }
    &--five{
        background-color: #457b9d
    }
    &--five:hover{
        background-color: #e63946;
        color: #a8dadc;
    }
    &--six{
        background-color:#1d3557;
    }
    &--six:hover{
        background-color: #e63946;
        color: #a8dadc;
    }           
    }

}