.mainly{
    display: flex;
    flex-direction: column;
    align-content: center;
    text-align: center;
    margin: 5px auto;
    min-height: 70vh;
    width: 100%;
    &__title{
        flex-basis: 100%;
        padding: 10px;
        font-size: 2.5em;
        letter-spacing: 3px;
        &--underliner{
            border-bottom: 2px solid black;
        }
    }
    &__container{
        flex-grow: 1;
    }
}

.container{
    display:flex;
    min-height: 80vh;
    flex-direction: column;
    @media(orientation: landscape){
        flex-direction: row;   
    }
    &__left{
        width: 100%;
        min-height: 400px;
        flex-basis: 50%;
        background-image: url('../images/beach-640.jpg');
        background-size: cover;
        @media (min-width: 640px){
            background-image: url('../images/beach-1280.jpg');
           }
        @media (min-width: 1280px){
            background-image: url('../images/beach-1920.jpg');
           }
    }
    &__right{
        background-color: cornsilk;
        width: 100%;
        flex-basis: 50%;
        border: 1px solid white;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-content: center;
        &--title{
            padding: 20px;
            font-size: 2.2em;
        }
        &--description{
            font-size: 1.2em;
            text-align: center;
            margin: 10px 25%;
            @media(min-width: 640px){
                font-size: 1.6em;
            }
            
        }
    }
}