.blocky{
    margin: 0 auto;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    align-content: center;
    &__element{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-content: center; 
        height: 400px;
        max-width: 700px;
        flex-basis: 100%;
        align-content: center;
        text-align: center;
        background-repeat: no-repeat;
        background-position: center;
        color: white;
        box-shadow: 0 0 20px 3px #073b4c;
    &--active{
        justify-content: space-evenly;
        display: flex;
    }
        @media(min-width: 568px){
            flex-basis: 45%;
            margin: 10px;
        }
    }
    &__element:hover{
        cursor: pointer;
    }

    &__title{
        font-size: 2.5em;
        padding: 10px;
        font-weight: 700;
        letter-spacing: 2px;
        opacity: .4;
        text-shadow: 3px 3px black;
        text-align: center;
    &--active{
        color: #222;
        font-size: 2.5em;
        margin-top: 20px;
        padding: 5px;
        @media(min-width: 360px){
            font-size: 1.5em;   
            }
        @media(min-width: 568px){
        font-size: 2.8em;   
        }
        @media(min-width: 1024px){
            font-size: 3.2em;   
            }
    }
    }    
    &__subtitle{
        font-size: 1.1em;
        padding: 10px;
        opacity: .4;
        text-shadow: 3px 3px black;
    &--active{
        font-size: 1.4em;
        color: #444;
        padding: 5px;
        @media(min-width: 360px){
            font-size: 1.4em;   
            }
        @media(min-width: 568){
            font-size: 1.6em;   
            }
    }
}
    &__info{
        text-decoration: underline;
        color: grey;
        font-size: 1.1em;
        font-weight: 700;
    &--active{
        color: #333;
        font-size: 1em;
        padding: 10px;
    @media(min-width: 360px){
            font-size: 1em;   
            }
    @media(min-width: 568px){
            font-size: 1em;   
            }
    @media(min-width: 1024px){
                font-size: 1.4em;   
                }
    }
    }
    &--1{
        background-image: url('https://picsum.photos/id/237/360/400');
        @media (orientation: landscape){
        background-image: url('https://picsum.photos/id/237/700/400');    
        }
        @media (min-width: 360px){
            background-image: url('https://picsum.photos/id/237/568/400');    
            }
        @media (min-width: 568px){
                background-image: url('https://picsum.photos/id/237/700/400');    
                }
    &--active{
        background-color: #ffd6a5;
        transition: 1s;
    }
    }
    &--2{
        background-image: url('https://picsum.photos/id/22/360/400');
        @media (orientation: landscape){
        background-image: url('https://picsum.photos/id/22/700/400');    
        }
        @media (min-width: 360px){
            background-image: url('https://picsum.photos/id/22/568/400');    
            }
        @media (min-width: 568px){
                background-image: url('https://picsum.photos/id/22/700/300');    
                }
    &--active{
        background-color: #fdffb6;
        transition: 1s;
    }
    }
    &--3{
        background-image: url('https://picsum.photos/id/85/360/400');
        @media (orientation: landscape){
        background-image: url('https://picsum.photos/id/85/700/400');
        }
        @media (min-width: 360px){
            background-image: url('https://picsum.photos/id/85/568/400');    
            }
        @media (min-width: 568px){
                background-image: url('https://picsum.photos/id/85/700/300');    
                }
    &--active{
        background-color: #caffbf;
        transition: 1s;
    }
    }
    &--4{
        background-image: url('https://picsum.photos/id/247/360/400');
        @media (orientation: landscape){
        background-image: url('https://picsum.photos/id/247/700/400');
        }
        @media (min-width: 360px){
            background-image: url('https://picsum.photos/id/247/568/400');    
            }
        @media (min-width: 568px){
                background-image: url('https://picsum.photos/id/247/700/400');    
                }
    &--active{
        background-color: #caffbf;
        transition: 1s;
    }
    }

    &--5{
        background-image: url('https://picsum.photos/id/220/360/400');
        @media (orientation: landscape){
