        }
        @media (min-width: 360px){
            background-image: url('https://picsum.photos/id/220/568/400');    
            }
        @media (min-width: 568px){
                background-image: url('https://picsum.photos/id/220/700/400');    
                }   
    &--active{
        background-color: #9bf6ff;
        transition: 1s;
    } 
    }

    &--6{
        background-image: url('https://picsum.photos/id/7/360/400');
        @media (orientation: landscape){
